<script>

// import RawMaterialController from "../../controllers/material_controller.js";

import props from "./options/props.js";
import computed from "./options/computed.js";
import methods from "./options/methods.js";
import components from "./options/components.js";
import watch from "./options/watch.js";
import data from "./options/data.js";

export default {
    name: "CostEstimation",
    props,
    data,
    watch,
    computed,
    methods,
    components,
    _mounted() {
        const self = this;
        const { $refs: refs } = this;

        let internal_change = false;

        const df = frappe.ui.form.make_control({
            parent: refs.tipo_de_producto,
            df: {
                fieldtype: "Link",
                options: "Product Type",
                fieldname: "product_type",
                label: "Tipo de Producto",
                reqd: 1,
                change(event) {
                    const { value } = this;

                    if (internal_change) {
                        internal_change = false;
                        return;
                    }

                    self.form_data.tipo_de_producto = value;
                    self.update_data();
                },
            },
            render_input: true,
        });

        internal_change = true;
        df.set_value(this.form_data.tipo_de_producto);
    },
};
</script>

